.weather {
    width: 100%;
    height: 325px;
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-content: space-between;
}

/* Left side of current weather tab */
.top {
    margin: 20px 0;
    width: 35%;
}

.date {
    /* font-size: 19px; */
    margin-bottom: -15px;
}

.city {
    font-size: 47px;
    font-weight: bold;
    margin: 0;
}

.temperature {
    position: absolute;
    display: flex;
    margin: 48px 0 0 0;
    font-size: 120px;
    font-weight: bold;
}

/* Middle section of Current Weather */
.middle {
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    width: 30%;
    text-align: center;
    background: linear-gradient(#0569ba, #6caadc08);
    border-radius: 25px;
    margin: 55px 0 15px 0;
    padding: 6px 25px 0 25px;
}

.weather-icon {
    margin: 0;
    width: 70%;
    align-self: center;
}

.weather-description {
    margin: 0;
    font-size: 40px;
    font-weight: bold;
    line-height: 25px;
}


/* Right Section of Current Weather */
.bottom {
    margin: 70px 0 0 0;
    width: 18%;
}

.details {
    display: inline-flex;
    flex-direction: column;
    align-items: end;
    gap: 24px;
}

.parameter-label-title {
    font-size: 47px;
    font-weight: bold;
}

.parameter-row {
    display: flex;
    margin: 8px 0;
}

.parameter-label {
    font-size: 21px;
    font-weight: bold;
}

.parameter-value {
    font-size: 21px;
    padding-left: 6px;
}

/* Converter */

.conversion-container {
    display: flex;
    font-size: 24px;
    margin-top: 24px;
}

.degrees {
    font-size: 56px;
    color: rgb(174, 173, 173);
    cursor: pointer;
    margin: 0;
}

.degrees.active{
    color: white;
    margin: 0;
    font-size: 90px;
    line-height: 72px;
}

.divider {
    height: 50px;
    padding: 2px;
    margin: 3px 10px;
    background-color: white;
}


@media (max-width: 1024px) {
    
    .top {
        width: 38%;
    }

    .temperature {
        margin: 80px 0 0 0;
        font-size: 140px;
    }

    .middle {
        width: 25%;
        justify-content: center;
    }

    .bottom {
        width: 21%;
    }

    
}

/* Horizontal tablet */
@media (max-width: 1366px) {

    .weather {
        height: 100%;
        flex-direction: column;
    }

    .weather-icon {
        width: 60%;
        margin: 22px 0 0 0;
    }

    .weather-description {
        line-height: 50px;
        font-size: 36px;
    }

    .temperature {
        top: 460px;
        display: flex;
        margin: 0 50px;
        font-size: 150px;
    }

    .top {
        width: 100%;
    }

    .middle {
        justify-content: center;
        width: 100%;
        padding: 0px;
        margin: 0px;
        background: linear-gradient(#0570c7, #6caadc08);
    }
    
    .bottom {
        width: 100%;
        margin: 180px 0 45px 0;
    }

    .details {
        gap: 8px;
        align-items: baseline;
        flex-direction: row;
    }

    .parameter-row {
        flex-direction: column;
        text-align: center;
        display: flex;
    }

    .parameter-label {
        font-size: 18px;
    }

    .parameter-value {
        font-size: 19px;
    }

    .degrees.active {
        line-height: 90px;;
    }

}
    @media (max-width: 390px) {

        .temperature {
            top: 450px;
        }
 }


